import { createStore } from "usestore-react";

if (typeof window !== 'undefined') {
  console.log('[KitOfParts] Initializing stores...');
  const storesToCreate = [
    "selectedObjectId",
    "kitOfParts_clickbathroom2",
    "kitOfParts_boxbedState",
    "kitOfParts_frontState",
    "kitOfParts_kitchenState",
    "kitOfParts_k2State",
    "kitOfParts_s1State",
    "kitOfParts_st1State",
    "kitOfParts_cameraState",
    "kitOfParts_objectsState",
    "kitOfParts_sceneRotation",
    "selectedNodeStore",
    "cameraOptions",
    "livingContinuity_sceneRotation",
    "walkThroughActive"
  ];
  
  storesToCreate.forEach(storeName => {
    try {
      const defaultValue = storeName === "kitOfParts_sceneRotation" ? true : 
                          storeName.includes("Store") || storeName === "cameraOptions" ? null : false;
      createStore(storeName, defaultValue);
      console.log(`[KitOfParts] Store "${storeName}" initialized with value:`, defaultValue);
    } catch (error) {
      console.log(`[KitOfParts] Store "${storeName}" already exists, skipping`);
    }
  });
  console.log('[KitOfParts] Store initialization complete');
  
  window.__kitOfPartsStoresInitialized = true;
}
